body {
  background-color: #222227;
}

.App {
  text-align: center;
  max-width: 728px;
  margin: 0 auto;
  
}

.App header {
  background-color: rgba(14, 14, 15, 0.685);
  backdrop-filter: blur(6px);
  height: 10vh;
  min-height: 50px;
  color: white;
  position: fixed;
  width: 100%;
  max-width: 728px;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
  padding: 10px;
  box-sizing: border-box;
}

.App section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  background: #05040c3c;
  
}
  

main {
  padding: 10px;
  height: 80vh;
  margin: 10vh 0 10vh;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}

main::-webkit-scrollbar {
  width: 0.25rem;
}

main::-webkit-scrollbar-track {
  background: #1e1e24;
}

main::-webkit-scrollbar-thumb {
  background: #6649b8;
}

form {
  height: 10vh;
  position: fixed;
  bottom: 0;
  background-color: rgb(24, 23, 23);
  width: 100%;
  max-width: 728px;
  display: flex;
  font-size: 1.5rem;
}

form button {
  width: 20%;
  background-color: rgba(14, 14, 15, 0.685);
  backdrop-filter: blur(6px);
}


input {
  line-height: 1.5;
  width: 100%;
  font-size: 1.3rem;
  background: rgb(24, 23, 23);
  color: white;
  outline: none;
  border: none;
  padding: 0 10px;
}

button {
  background-color: #282c34; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: 1.25rem;
}


button.sign-out {
  border-radius: 30px;
  font-size: 18px;
}

button.sign-out:active {
  
  opacity: .6;
}



  button.sign-in:hover{
  background-color: #111115;
  color: white;
  transition: 1s;
  animation: cdx 3s ;  

}

@keyframes cdx {

    30% {
      padding: 15px 60px;
    transition:  ease-in-out;

    }
  70% {
    padding: 15px 32px;
    transition: ease-in-out;

    
  }
  
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}


.sign-in {
  color: #282c34;
  background: white;
  max-width: 400px;
  margin: 0 auto;
  border-radius: 30px;
}




ul, li {
  text-align: left;
  list-style: none;
}

p a.sui {
  text-decoration: none;
  color: white;
  background: black;
  padding: 13px 30px;
  border-radius: 30px;
  box-shadow: inset 0px 0px 7px 0px red;
  transition: 2s;
}

p a.sui:hover {
  box-shadow: 0px 0px 7px 0px red;
  transition: 2s;


}

p {

  max-width: 682px;
  margin-bottom: 12px;
  line-height: 24px;
  padding: 10px 20px;
  border-radius: 25px;
  position: relative;
  color: white;
  text-align: center;
}

.message {
  display: flex;
  align-items: center;
}


.sent {
  flex-direction: row-reverse;
}

.sent p {
  color: white;
  background: #0b93f6;
  align-self: flex-end;
}
.received p {
  background: #e5e5ea;
  color: black;
}

img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 2px 5px;
}
